import React, { FC } from 'react';
import { InputGroupInline, InputProps, InputType } from 'summer';
import { CLASS_NAME } from '../../../../models/dtos/mfp/cbo';

const CboIS: FC<InputProps> = props => {
  return (
    <InputGroupInline
      {...props}
      type={InputType.INPUT_SEARCH}
      inputSearchProps={{
        ...props.inputSearchProps,
        columns: [
          {
            dataField: 'codigo',
            text: 'Código',
          },
          {
            dataField: 'descricao',
            text: 'Descrição',
          },
          {
            dataField: 'professor',
            text: 'Salário aula',
          },
        ],
        dtoClassName: CLASS_NAME,
        searchViewId: 'CboSearchView',
      }}
    />
  );
};

export default CboIS;
