import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, onlyAlphaNum, WindowComponent } from 'summer';
import { Cargo, CLASS_NAME } from '../../../../models/dtos/mfp/cargo';
import CboIS from '../../../components/mfp/cboIS/CboIS';

interface CargoEditViewProps extends EditViewProps<Cargo> {}

const maxWidthLabel = 110;

const CargoEditView: FC<CargoEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<Cargo>({
    defaultValues: selectedItem,
    mode: 'onBlur',
  });

  // TODO FAZER INCLUSÃO E ALTERAÇÃO DE EVENTOS PENDENTES DO E-SOCIAL

  return (
    <WindowComponent title="Manutenção de CBO">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { ...onlyAlphaNum(0, 6) } }}
              id="codigo"
              isPrependLabel={true}
              labelWidth={maxWidthLabel}
              label="Código"
              placeholder="Informe o Código do CBO."
              tooltipProps={{ message: 'Preenchimento Opcional - Codificação Automática.' }}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true, maxLength: 250 } }}
              id="descricao"
              isPrependLabel={true}
              labelWidth={maxWidthLabel}
              label="Descrição"
              placeholder="Informe a descrição."
              type={InputType.TEXT_AREA}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <CboIS id="cbo" isPrependLabel={true} labelWidth={maxWidthLabel} label="CBO" placeholder="Informe o CBO." formProps={formProps} />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default CargoEditView;
